d color on hover */\n}\n\n.list-item-small {\n  /* Add individual list item styles here */\n  flex: 0 0 8%;\n  margin:4px;\n}\n\n.list-item-medium {\n  /* Add individual list item styles here */\n  flex: 0 0 18%;\n  margin-right:2px;\n}\n\n.list-item-large {\n  /* Add individual list item styles here */\n  flex: 0 0 22%;\n  margin:4px;\n}\n\n.bottom-button{\n  padding:2rem;\n  position: absolute;\n  right: 0;\n\n}\n"],"sourceRoot":""}]);
